<template>
    <view class="friend-item" @click="toChatBody(bodyData)">
        <view style="position: relative;">
            <user-card :ovoUserInfo="bodyData.otherUser" :bottomTextShow="false">
            </user-card>
            <view style="height: 5rpx;"></view>
            <view v-if="bodyData.lastChat != null || bodyData.lastChat != ''" class="friend-item-info-msg">
                <template v-if="bodyData.typeCode == 'privateChat'">
                    <text v-if="bodyData.lastChat.userIdOfSend == ovoUserSelf.userId" :style="{
                        'fontSize': '28rpx',
                        'color': '#999999'
                    }">
                        {{ '我：' }}
                    </text>

                    <text v-else :style="{
                        'fontSize': '28rpx',
                        'color': '#999999'
                    }">
                        {{ bodyData.otherUser.gender == 0 ? "她：" : "他：" }}
                    </text>

                </template>

                <text v-if="bodyData.lastChat.typeCode == 'text'" :style="{
                    'fontSize': '28rpx',
                    'color': '#999999'
                }">{{ bodyData.lastChat.content }}</text>
                <text v-else-if="bodyData.lastChat.typeCode == 'image'" :style="{
                    'fontSize': '28rpx',
                    'color': '#999999'
                }">[图片]</text>
                <text v-else-if="bodyData.lastChat.typeCode == 'video'" :style="{
                    'fontSize': '28rpx',
                    'color': '#999999'
                }">[视频]</text>
                <text v-else-if="bodyData.lastChat.typeCode == 'voice'" :style="{
                    'fontSize': '28rpx',
                    'color': '#999999'
                }">[语音]</text>
            </view>
        </view>
    </view>
</template>


<script>

export default {
    props: ["item"],
    data() {
        return {
            ovoUserSelf: { userId: "" },
            bodyData: {
                fileIdOfAvatar: '',
                name: '',
                lastChat: {
                    typeCode: '',
                    content: '',
                    userIdOfSend: ''
                },

            },
            lastChatText: '',
        }
    },
    mounted() {
        this.ovoUserSelf = this.$ovo.getSelf();
        this.bodyData = this.item;
        uni.$on("newChat:roomId:" + this.bodyData.roomId, (data) => {
            this.bodyData.lastChat = data;
        });
    },
    methods: {
        toChatBody(room) {
            uni.setStorageSync('roomId:' + room.id + ":room", room)
            this.$kt.util.navTo('/pages/public/chat-body?roomId=' + room.id);
        },
    },
    watch: {
        item: {
            handler(val, oldVal) {
                this.bodyData = val;
                console.log("roomItem.vue: ", this.bodyData);
            },
            deep: true,
            immediate: true
        }
    },
}
</script>

<style lang="scss" scoped>
.friend-item {
    width: 100%;
    padding: 20rpx;
    box-sizing: border-box;
}

.friend-item-info-msg {
    position: absolute;
    bottom: 0rpx;
    left: 130rpx;
    height: 40rpx;
    // 不换行
    white-space: nowrap;
    // 超出部分隐藏
    overflow: hidden;
    // 文字超出部分用省略号显示
    text-overflow: ellipsis;
    // 最大宽度
    max-width: 400rpx;
}
</style>